import { Component, OnInit, ViewChild, TemplateRef, Output, EventEmitter, Input, AfterViewInit, ElementRef } from '@angular/core';
import { WidgetActionType } from '../core/enums/widget-action-type.enum';

@Component({
  selector: 'cmacs-kpi-group',
  templateUrl: './cmacs-kpi-group.component.html',
  styleUrls: ['./cmacs-kpi-group.component.css']
})
export class CmacsKpiGroupComponent implements OnInit, AfterViewInit {

  @ViewChild('columnTemplate', { read: TemplateRef }) columnTemplate: TemplateRef<{}>;
  @ViewChild('chartcanvas', {read: ElementRef }) canvasRef: ElementRef;

  @Output() clickMenu = new EventEmitter<any>();

  // container
  @Input() headerText = '';
  @Input() footerText = '';
  @Input() footerValue = '';

  // content
  @Input() data!: any[];
  @Input() columnsHeader: string[] = [];
  @Input() width = 104;

  dataTable: any[];
  configurationExpandableRows: any;
  loading = true;
  selectedItem!: string;

  constructor() { }

  ngOnInit() {
  }

  ngAfterViewInit() {
    if (this.data.length > 0) {
      this.selectedItem = this.data[0].key;
    }
    this.drawCanvas();
    this.setConfiguration();
    this.setData();
  }

  drawCanvas(): void {
    const item = this.data.find(x => x.key === this.selectedItem);
    if (this.canvasRef && item) {
      const canvas = this.canvasRef.nativeElement as HTMLCanvasElement;
      canvas.width = this.width;
      canvas.height = this.width;
      //
      const ctx = canvas.getContext('2d');
      ctx.clearRect(0, 0, canvas.width, canvas.height);

      // totals
      const totalShow = this.getTotalCateg(item.data);
      const total = this.data.map(x => this.getTotalCateg(x.data)).reduce((a, b) => a + b, 0);
      let startAngle = 4;

      // draw data
      for (const categ of this.data) {
        const totalG =  this.getTotalCateg(categ.data);
        const sliceAngle = 2 * Math.PI * totalG / total;
        this.drawPieSlice(
          ctx,
          canvas.width / 2,
          canvas.height / 2,
          Math.min(canvas.width / 2, canvas.height / 2) - ((this.selectedItem === categ.key) ? 10 : 6),
          startAngle,
          startAngle + sliceAngle - 0.05,
          categ.color,
          (this.selectedItem === categ.key) ? 15 : 7
        );
        startAngle += sliceAngle;
      }
      // draw value
      ctx.font = '20px Roboto';
      ctx.fillStyle = '#3b3f46';
      ctx.textAlign = 'center';
      ctx.fillText( '' + totalShow, canvas.width / 2, canvas.width / 2 + 5)
    }
  }

  drawPieSlice(ctx: CanvasRenderingContext2D, centerX: number, centerY: number,
               radius: number, startAngle: number, endAngle: number, color: any, lineWidth: number ) {
    ctx.beginPath();
    ctx.arc(centerX, centerY, radius, startAngle, endAngle);
    ctx.lineWidth = lineWidth;
    ctx.strokeStyle = color;
    ctx.stroke();
  }

  getTotalCateg(data: any[]): number {
    return data.map(x => x.value).reduce((a, b) => a + b, 0);
  }

  setConfiguration(): void {
    this.configurationExpandableRows = {
      fields: [
        {
          celdType: 3,
          display: '',
          property: 'color',
          width: '20%',
        },
        {
          celdType: 0,
          display: this.columnsHeader[0],
          property: 'name',
          editTemplate: 3,
          width: '60%',
        },
        {
          celdType: 0,
          display: this.columnsHeader[1],
          property: 'value',
          width: '20%',
          editTemplate: 2,
          editable: false
        }
      ],
      fieldId: 'key',
      fieldRate: 'fav'
    };
  }

  setData(): void {
    this.loading = true;
    const temp: any[] = [];
    if (this.selectedItem) {
      const data = this.data.find(x => x.key === this.selectedItem);
      for (const item of data.data) {
        const newItem = {
          key: item.key,
          name: item.name,
          color:  {
            ref: this.columnTemplate,
            context: {
              color: data.color
            }
          },
          value: item.value,
          children: this.getChildren(item.rows)
        };
        temp.push(newItem);
      }
      this.dataTable = temp;
    }
    setTimeout(() => {
      this.loading = false;
    }, 0);
  }
  getChildren(items: any[]): any[] {
    const output: any[] = [];
    for (const item of items) {
      output.push({
        key: item.key,
        name: item.name,
        color: {},
        value: ''
      });
    }
    return output;
  }

  menuClick(type: WidgetActionType): void {
    this.clickMenu.emit(type);
  }

  changeData(key: string) {
    this.selectedItem = key;
    this.setData();
    this.drawCanvas();
  }
}
