import { Component, OnInit, Output, EventEmitter, Input, AfterViewInit, ViewChild, TemplateRef } from '@angular/core';
import { UtilService } from '../core/services/util.service';
import { WidgetActionType } from '../core/enums/widget-action-type.enum';

@Component({
  selector: 'cmacs-status-distribution',
  templateUrl: './cmacs-status-distribution.component.html',
  styleUrls: ['./cmacs-status-distribution.component.css']
})
export class CmacsStatusDistributionComponent implements OnInit, AfterViewInit {

  @ViewChild('columnTemplate', { read: TemplateRef }) columnTemplate: TemplateRef<{}>;

  @Output() clickMenu = new EventEmitter<any>();

  // content
  @Input() data!: any[];
  @Input() columnsHeader: string[] = [];

  id;
  dataTable: any[];
  configurationExpandableRows: any;

  constructor(
    private util: UtilService
  ) {
    this.id = util.uuidv4();
  }

  ngOnInit() {
  }

  ngAfterViewInit() {
    this.drawCanvas();
    this.setConfiguration();
    setTimeout(() => {
      this.setData();
    }, 0);
  }

  menuClick(type: WidgetActionType): void {
    this.clickMenu.emit(type);
  }

  setConfiguration(): void {
    this.configurationExpandableRows = {
      fields: [
        {
          celdType: 3,
          display: '',
          property: 'color',
          width: '20%',
        },
        {
          celdType: 0,
          display: this.columnsHeader[0],
          property: 'name',
          editTemplate: 3,
          width: '60%',
        },
        {
          celdType: 0,
          display: this.columnsHeader[1],
          property: 'value',
          width: '20%',
          editTemplate: 2,
          editable: false
        }
      ],
      fieldId: 'key',
      fieldRate: 'fav'
    };
  }

  setData(): void {
    const temp: any[] = [];
    for (const item of this.data) {
      const newItem = {
        key: item.key,
        name: item.name,
        color: {
          ref: this.columnTemplate,
          context: {
            color: item.color
          }
        },
        value: item.value,
        children: []
      };
      for (let i = 0; i < item.rows.length; i++) {
        newItem.children.push({
          key: item.key + '-' + i,
          name: item.rows[i],
          color: { },
          value: ''
        });
      }
      temp.push(newItem);
    }
    this.dataTable = temp;
  }

  drawCanvas(): void {
    const canvas = document.getElementById('canvas-' + this.id)  as HTMLCanvasElement;
    canvas.height = 40;
    canvas.width -= 7;
    if (canvas.getContext) {
      const context = canvas.getContext('2d');
      const total = this.data.map(x => x.value).reduce((a, b) => a + b, 0);
      let move = 0;
      for (const item of this.data) {
        context.beginPath();
        context.moveTo(move, 1);
        context.lineWidth = 10;
        const val = item.value;
        move += val / total * canvas.width;
        context.strokeStyle = item.color;
        context.lineTo(move - 5, 1);
        context.stroke();
      }
    }
  }
}
